@use "../../global/" as g;

.l-contact-modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: g.$layer-modal;
  width: calc(100% - #{g.rem(24)} * 2);
  height: calc(100vh - #{g.rem(32)} * 2);
  padding: g.rem(56) g.rem(24) g.rem(32);

  @include g.mq("md") {
    width: calc(100% - #{g.rem(60)} * 2);
    height: calc(100vh - #{g.rem(120)} * 2);
    padding: g.rem(56);
  }
}
