@use "../../../global/" as g;

.p-contact-form__items {
  width: 100%;
  max-width: 100%;

  @include g.mq("md") {
    max-width: g.rem(696);
    margin-right: auto;
  }
}

.p-contact-form__item:nth-child(n + 2) {
  margin-top: g.rem(32);

  @include g.mq("md") {
    margin-top: g.rem(45);
  }
}

.p-contact-form__checkbox {
  text-align: center;
  margin-top: g.rem(40);

  @include g.mq("md") {
    margin-top: g.rem(48);
  }
}

.p-contact-form__button {
  text-align: center;
  margin-top: g.rem(48);

  @include g.mq("md") {
    margin-top: g.rem(56);
  }
}
