@use "../../global/" as g;

.c-section-subtitle {
  display: inline-block;
  color: rgba(#F9F871, 0.2);
  font-family: g.$font-family-sub;
  font-size: g.rem(50);
  letter-spacing: calc(2 / 50 * 1em);
  line-height: 1.2;
  text-transform: capitalize;

  @include g.mq("md") {
    font-size: g.rem(110);
  }
}
