@use "../../global/" as g;

.l-header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: g.$layer-header;
  width: 100%;
  height: g.rem(g.strip-unit(g.$header-height));
}

.l-header__inner {
  width: 100%;
  max-width: 100%;
  height: inherit;
  padding-left: g.rem(22);

  @include g.mq("md") {
    padding-left: g.rem(32);
  }
}
