@use "../../../global/" as g;

.p-lower-news__inner {
  width: 100%;
  max-width: g.rem(540 + g.strip-unit(g.$padding-sp) * 2);
  margin: 0 auto;
  padding: 0 g.rem(g.strip-unit(g.$padding-sp));

  @include g.mq("md") {
    max-width: g.rem(860 + g.strip-unit(g.$padding-pc) * 2);
    padding: 0 g.rem(g.strip-unit(g.$padding-pc));
  }
}

.p-lower-news__item:nth-child(n + 2) {
  margin-top: g.rem(32);
}

.p-lower-news__pagination {
  margin-top: g.rem(80);

  @include g.mq("md") {
    margin-top: g.rem(104);
  }
}
