@use "../../global/" as g;

.c-fixed-to-top {
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s, visibility 0.3s;
}

.c-fixed-to-top.is-show {
  opacity: 1;
  visibility: visible;
}

.c-fixed-to-top a {
  display: block;
  width: g.rem(45);
  height: g.rem(45);
  background-color: #fff;
  position: relative;
  transition: background-color 0.3s;
}

.c-fixed-to-top a:focus,
.c-fixed-to-top a:hover {
  @include g.mq("md") {
    background-color: #333;
  }
}

.c-fixed-to-top a::after {
  content: "";
  display: block;
  position: absolute;
  top: 42%;
  left: 50%;
  width: g.rem(16);
  height: g.rem(16);
  border-left: g.rem(2) solid #333;
  border-top: g.rem(2) solid #333;
  transform: translateX(-50%) rotate(45deg);
  transition: border-color 0.3s;
}

.c-fixed-to-top a:focus::after,
.c-fixed-to-top a:hover::after {
  @include g.mq("md") {
    border-color: #fff;
  }
}
