@use "../../../global/" as g;

.p-input-group-checkbox label {
  position: relative;
  display: inline-block;
}

.p-input-group-checkbox input[type="checkbox"] {
  position: absolute;
  top: 0;
  left: 0;
  width: g.rem(24);
  height: g.rem(24);
  opacity: 0;
  margin: 0;
}

.p-input-group-checkbox input[type="checkbox"] + span {
  display: inline-block;
  vertical-align: text-top;
  padding-left: g.rem(34);
  font-size: g.rem(16);
  letter-spacing: calc(0.2 / 16 * 1em);
  line-height: 1.2;
}

.p-input-group-checkbox input[type="checkbox"] + span::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: g.rem(24);
  height: g.rem(24);
  border: g.rem(1) solid #111;
  background-color: #fff;
}

.p-input-group-checkbox input[type="checkbox"]:focus + span::before {
  outline: g.rem(2) solid #FFEA2E;
}

.p-input-group-checkbox input[type="checkbox"] + span::after {
  content: "";
  display: block;
  position: absolute;
  top: g.rem(4);
  left: g.rem(4);
  width: g.rem(16);
  height: g.rem(10);
  border-left: g.rem(2) solid #111;
  border-bottom: g.rem(2) solid #111;
  transform: rotate(-45deg);
  opacity: 0;
  transition: opacity 0.3s;
}

.p-input-group-checkbox input[type="checkbox"]:checked + span::after {
  opacity: 1;
}

.p-input-group-checkbox input[type="checkbox"] + span > a {
  color: #FFEA2E;
  font-weight: g.$bold;
  text-decoration: underline;
}
