@use "../../../global/" as g;

.p-related-post-card {
  display: flex;
  flex-direction: column;
  background-color: #fff;
  position: relative;
  transition: background-color 0.3s;
}

.p-related-post-card:focus,
.p-related-post-card:hover {
  @include g.mq("md") {
    background-color: #333;
  }
}

.p-related-post-card__image {
  position: relative;
}

.p-related-post-card__image::before {
  content: "";
  display: block;
  padding-top: g.aspect-ratio(223, 335);

  @include g.mq("md") {
    padding-top: g.aspect-ratio(233, 350);
  }
}

.p-related-post-card__image img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.p-related-post-card__info {
  padding: g.rem(5) g.rem(23) 0;

  @include g.mq("md") {
    padding: g.rem(5) g.rem(24) 0;
  }
}

.p-related-post-card__title {
  color: #333;
  font-size: g.rem(18);
  font-weight: g.$medium;
  letter-spacing: calc(1 / 18 * 1em);
  line-height: g.lh(35, 18);
  transition: color 0.3s;
  
  @include g.mq("md") {
    font-size: g.rem(14);
    line-height: g.lh(35, 14);
  }
}

.p-related-post-card:focus .p-related-post-card__title,
.p-related-post-card:hover .p-related-post-card__title {
  @include g.mq("md") {
    color: #fff;
  }
}

.p-related-post-card__meta {
  padding: g.rem(14) g.rem(23) g.rem(16);
  margin-top: auto;
  display: flex;
  align-items: center;
  
  @include g.mq("md") {
    padding: g.rem(14) g.rem(24) g.rem(16);
  }
}

.p-related-post-card__category {
  display: inline-block;
  color: #000;
  font-size: g.rem(14);
  letter-spacing: calc(1 / 14 * 1em);
  line-height: 1.2;
  padding: g.rem(3) g.rem(3) g.rem(4);
  background-color: #FFEA2E;
}

.p-related-post-card__published {
  display: inline-block;
  color: #333;
  font-size: g.rem(14);
  letter-spacing: calc(1 / 14 * 1em);
  line-height: 1.2;
  margin-left: auto;
  transition: color 0.3s;
}

.p-related-post-card:focus .p-related-post-card__published,
.p-related-post-card:hover .p-related-post-card__published {
  @include g.mq("md") {
    color: #fff;
  }
}
