@use "../../../global/" as g;

.p-works-gallery-main {
  overflow: hidden;
  position: relative;
}

.p-works-gallery-main__slide {
  position: relative;
}

.p-works-gallery-main__slide::before {
  content: "";
  display: block;
  padding-top: g.aspect-ratio(251, 375);

  @include g.mq("md") {
    padding-top: g.aspect-ratio(427, 800);
  }
}

.p-works-gallery-main__slide img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.p-works-gallery-main__button-prev,
.p-works-gallery-main__button-next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: g.rem(30);
  height: g.rem(30);
  margin-top: 0;

  @include g.mq("md") {
    width: g.rem(50);
    height: g.rem(50);
  }
}

.p-works-gallery-main__button-prev {
  left: g.rem(8);
  background: transparent url(../img/works-detail/works-detail_button-prev.svg) no-repeat center center / contain;
}

.p-works-gallery-main__button-prev::after {
  content: "";
}

.p-works-gallery-main__button-next {
  right: g.rem(8);
  background: transparent url(../img/works-detail/works-detail_button-next.svg) no-repeat center center / contain;
}

.p-works-gallery-main__button-next::after {
  content: "";
}
