@use "../../../global/" as g;

.p-blog-archive__items {
  display: grid;
  grid-template-columns: 1fr;
  gap: g.rem(24);
  margin-top: g.rem(21);

  @include g.mq("md") {
    grid-template-columns: repeat(3, 1fr);
    column-gap: g.rem(25);
    row-gap: g.rem(48);
    margin-top: g.rem(24);
  }
}

.p-blog-archive__pagination {
  margin-top: g.rem(80);

  @include g.mq("md") {
    margin-top: g.rem(104);
  }
}
