@use "../../../global/" as g;

.p-data-block {
  display: flex;
  border-bottom: g.rem(1) solid #fff;
}

.p-data-block__left,
.p-data-block__right {
  // padding-top: g.rem(14);
  // padding-bottom: g.rem(19);
  font-size: g.rem(13);

  @include g.mq("md") {
    // padding-top: g.rem(33);
    // padding-bottom: g.rem(28);
    font-size: g.rem(16);
  }
}

.p-data-block__left {
  flex: 0 0 g.rem(114);
  // padding-left: g.rem(5);
  line-height: g.rem(21);

  @include g.mq("md") {
    flex: 0 0 g.rem(157);
  }
}

.p-data-block__right {
  flex: 0 1 calc(100% - #{g.rem(114)});
  line-height: g.rem(24);

  @include g.mq("md") {
    flex: 0 1 calc(100% - #{g.rem(157)});
  }
}
