@use "../../../global/" as g;

.p-point__title {
  display: inline-block;
  border: g.rem(1) solid #fff;
  border-radius: g.rem(32);
  background-color: #111;
  padding: g.rem(10) g.rem(23) g.rem(10) g.rem(24);
  font-size: g.rem(20);
  letter-spacing: calc(0.4 / 20 * 1em);
  line-height: 1.2;
  margin-left: g.rem(24);

  @include g.mq("md") {
    margin-left: g.rem(38);
  }
}

.p-point__text-wrapper {
  border: g.rem(1) solid #fff;
  padding: g.rem(55) g.rem(10) g.rem(32);
  margin-top: g.rem(-23);

  @include g.mq("md") {
    padding: g.rem(43) g.rem(22) g.rem(32) g.rem(22);
  }
}

.p-point__text {
  font-size: g.rem(16);
  letter-spacing: calc(0.4 / 16 * 1em);
  line-height: g.lh(35, 16);
}
