@use "../global/" as g;

.wp-pagenavi {
  display: flex;
  justify-content: center;
}

.wp-pagenavi a,
.wp-pagenavi span {
  display: inline-block;
  border: g.rem(1) solid #fff;
  padding: g.rem(3) g.rem(6);
  font-size: g.rem(16);
  letter-spacing: calc(0.5 / 16 * 1em);
  line-height: 1;
  margin: 0 g.rem(6);

  @include g.mq("md") {
    margin: 0 g.rem(8);
  }
}

.wp-pagenavi a {
  background-color: #333;
  color: #fff;
  transition: background-color 0.3s, color 0.3s;
}

.wp-pagenavi a:focus,
.wp-pagenavi a:hover {
  @include g.mq("md") {
    background-color: #fff;
    color: #333;
  }
}

.wp-pagenavi span {
  color: #333;
  background-color: #fff;
}

.wp-pagenavi a.previouspostslink,
.wp-pagenavi a.nextpostslink {
  padding: g.rem(3) g.rem(14);
  text-transform: uppercase;
}

.wp-pagenavi a.previouspostslink {
  margin-right: g.rem(12);
  margin-left: 0;

  @include g.mq("md") {
    margin-right: g.rem(15);
  }
}

.wp-pagenavi a.nextpostslink {
  margin-right: 0;
  margin-left: g.rem(12);
  
  @include g.mq("md") {
    margin-left: g.rem(15);
  }
}
