@use "../../../global/" as g;

.p-overview__inner {
  overflow: hidden;
}

.p-overview__subtitle {
  text-align: center;

  @include g.mq("md") {
    text-align: left;
  }
}

.p-overview__title {
  text-align: center;
  margin-top: g.rem(-16);

  @include g.mq("md") {
    margin-top: g.rem(-92);
  }
}

.p-overview__media {
  margin-top: g.rem(48);
  padding-bottom: g.rem(21);
  
  @include g.mq("md") {
    margin-top: g.rem(38);
    padding: g.rem(80) 0;
  }
}
