@use "../../../global/" as g;

.p-input-group-radio {
  @include g.mq("md") {
    display: flex;
  }
}

.p-input-group-radio__title {
  @include g.mq("md") {
    flex: 0 0 g.rem(158);
    padding-top: g.rem(2);
    padding-right: g.rem(24);
    text-align: right;
  }
}

.p-input-group-radio__title label {
  display: block;
  font-size: g.rem(14);
  letter-spacing: calc(0.7 / 14 * 1em);
  // line-height: g.lh(24, 14);
  line-height: 1.2;

  @include g.mq("md") {
    // line-height: g.lh(26, 14);
    line-height: 1.2;
  }
}

.p-input-group-radio__input {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-top: g.rem(16);
  margin-bottom: g.rem(-16);

  @include g.mq("md") {
    margin-top: 0;
    margin-bottom: 0;
  }
}

.p-input-group-radio__input label {
  position: relative;
  display: inline-block;
}

.p-input-group-radio__input input[type="radio"] {
  position: absolute;
  top: 0;
  left: 0;
  width: g.rem(20);
  height: g.rem(20);
  opacity: 0;
  margin: 0;
}

.p-input-group-radio__input input[type="radio"] + span {
  display: inline-block;
  vertical-align: text-top;
  font-size: g.rem(16);
  letter-spacing: calc(0.2 / 16 * 1em);
  line-height: 1.2;
  padding-left: g.rem(30);
  margin-right: g.rem(24);
  margin-bottom: g.rem(16);

  @include g.mq("md") {
    margin-bottom: 0;
  }
}

.p-input-group-radio__input input[type="radio"] + span::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: g.rem(20);
  height: g.rem(20);
  border: g.rem(1) solid #111;
  border-radius: 50%;
  background-color: #fff;
}

.p-input-group-radio__input input[type="radio"]:focus + span::before {
  outline: g.rem(2) solid #FFEA2E;
}

.p-input-group-radio__input input[type="radio"] + span::after {
  content: "";
  display: block;
  position: absolute;
  top: g.rem(4);
  left: g.rem(4);
  width: g.rem(12);
  height: g.rem(12);
  border-radius: 50%;
  background-color: #111;
  opacity: 0;
  transition: opacity 0.3s;
}

.p-input-group-radio__input input[type="radio"]:checked + span::after {
  opacity: 1;
}
