// 色
$background: #111; // 背景
$text-color: #fff; // テキスト色

// マウスホバー
$opacity: 0.7; // 透過度

// フォント
$font-family-base: 'Noto Sans JP', sans-serif;// ベースフォント
$font-family-sub: 'Noto Serif JP', serif;
$font-size-base: 16px; // ベースフォントサイズ
$font-weight-base: 400; // ベースフォントウェイト
$lh: 1.6; // ベース行間

// font-weight
$light: 300;
$regular: 400;
$medium: 500;
$semi-bold: 600;
$bold: 700;
$extra-bold: 800;

// ---
// レイアウト
// ---
$l-inner: 1100px; // インナー幅

$padding-pc: 32px; // タブレット時の余白
$padding-sp: 20px; // スマホ時の余白

// ブレークポイント（SP基準）
$breakpoints: (
  "sm": "(min-width: 375px)",
  "md": "(min-width: 768px)",
  "lg": "(min-width: 960px)",
	"xl": "(min-width: #{$l-inner})", // 1100px
  "xxl": "(min-width: 1440px)",
) !default;

// z-index
$layer-modal: 100;
$layer-modal-overlay: 99;
$layer-header-item: 41;
$layer-drawer: 40;
$layer-drawer-overlay: 39;
$layer-header: 30;
$layer-floating: 20;
$layer-footer: 10;
$layer-up: 2;
$layer-default: 1;
$layer-background: -1;

// ヘッダーの高さ
$header-height: 50px;