@use "../../../global/" as g;

.p-works-gallery-thumbs {
  overflow: hidden;
}

.p-works-gallery-thumbs__slide {
  position: relative;
}

.p-works-gallery-thumbs__slide::before {
  content: "";
  display: block;
  padding-top: 100%;
  
}

.p-works-gallery-thumbs__slide img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
