@use "../../global/" as g;

.c-button-news {
  display: inline-block;
  width: 100%;
  max-width: g.rem(360);
  border: g.rem(1) solid #fff;
  background-color: #333; 
  padding: g.rem(12) g.rem(24) g.rem(12) g.rem(24);
  color: #fff;
  font-size: g.rem(20);
  letter-spacing: calc(2 / 20 * 1em);
  line-height: 1.2;
  text-align: center;
  
  @include g.mq("md") {
    max-width: g.rem(134);
    border: none;
    background-color: #fff;
    padding: g.rem(5) g.rem(10) g.rem(6);
    color: #000;
    font-size: g.rem(16);
    font-weight: g.$medium;
    letter-spacing: 0;
    transition: color 0.3s;
    overflow: hidden;
    position: relative;
    z-index: g.$layer-default;
  }
}

.c-button-news:focus,
.c-button-news:hover {
  @include g.mq("md") {
    color: #fff;
  }
}

.c-button-news::after {
  @include g.mq("md") {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    z-index: g.$layer-background;
    width: 100%;
    height: 100%;
    background-color: #333;
    transform: translateX(-100%);
    transition: transform 0.3s;
  }
}

.c-button-news:focus::after,
.c-button-news:hover::after {
  @include g.mq("md") {
    transform: translateX(0);
  }
}
