@use "../../../global/" as g;

.p-privacy-policy__block:nth-child(n + 2) {
  margin-top: g.rem(32);

  @include g.mq("md") {
    margin-top: g.rem(48);
  }
}

.p-privacy-policy__heading {
  color: #333;
  font-size: g.rem(20);
  font-weight: g.$medium;
  letter-spacing: calc(0.2 / 20 * 1em);
  line-height: 2;
  padding-left: g.rem(9);
  border-left: g.rem(3) solid #888;
  
  @include g.mq("md") {
    font-size: g.rem(24);
    letter-spacing: calc(0.24 / 24 * 1em);
    padding-left: g.rem(12);
  }
}

.p-privacy-policy__text-wrapper {
  margin-top: g.rem(24);
}

.p-privacy-policy__text {
  color: #333;
  font-size: g.rem(16);
  letter-spacing: calc(0.2 / 16 * 1em);
  line-height: 2;
}

.p-privacy-policy__text:nth-child(n + 2) {
  margin-top: g.rem(16);
}
