@use "../../global/" as g;

.c-hamburger {
  cursor: pointer;
  padding: 0 g.rem(22);

  @include g.mq("md") {
    display: none;
  }
}

.c-hamburger__bars {
  display: block;
  width: g.rem(24);
  height: g.rem(12);
  position: relative;
}

.c-hamburger__bar {
  display: block;
  position: absolute;
  left: 0;
  width: 100%;
  height: g.rem(2);
  background-color: #fff;
}

.c-hamburger__bar:nth-child(1) {
  top: 0;
  transition: top 0.3s, transform 0.3s;
}

.c-hamburger.is-open .c-hamburger__bar:nth-child(1) {
  top: g.rem(6);
  transform: rotate(45deg);
}

.c-hamburger__bar:nth-child(2) {
  top: g.rem(5);
  transform: opacity 0.3s;
}

.c-hamburger.is-open .c-hamburger__bar:nth-child(2) {
  opacity: 0;
}

.c-hamburger__bar:nth-child(3) {
  top: g.rem(10);
  transition: top 0.3s, transform 0.3s;
}

.c-hamburger.is-open .c-hamburger__bar:nth-child(3) {
  top: g.rem(6);
  transform: rotate(-45deg);
}
