@use "../../../global/" as g;

.p-input-group {
  @include g.mq("md") {
    display: flex;
  }
}

.p-input-group__title {
  @include g.mq("md") {
    flex: 0 0 g.rem(158);
    padding-top: g.rem(14);
    padding-right: g.rem(24);
    text-align: right;
  }
}

.p-input-group__title label {
  display: block;
  font-size: g.rem(14);
  letter-spacing: calc(0.7 / 14 * 1em);
  // line-height: g.lh(24, 14);
  line-height: 1.2;

  @include g.mq("md") {
    // line-height: g.lh(26, 14);
    line-height: 1.2;
  }
}

.p-input-group__input {
  margin-top: g.rem(16);

  @include g.mq("md") {
    margin-top: 0;
    flex: 0 1 calc(100% - #{g.rem(156)});
  }
}

.p-input-group__input input[type="text"],
.p-input-group__input textarea {
	appearance: none;
	background-color: #fff;
	background-image: none;
	border: none;
	border-radius: 0;
	color: #333;
	font-family: inherit;
	font-size: g.rem(16);
	padding: g.rem(10) g.rem(9) g.rem(11);
	width: 100%;
}

.p-input-group__input input[type="text"]:focus,
.p-input-group__input textarea:focus {
  box-shadow: none;
  outline: 2px solid #FFEA2E;
}

.p-input-group__input textarea {
  height: g.rem(250);
  resize: vertical;

  @include g.mq("md") {
    height: g.rem(300);
  }
}
