@use "../../../global/" as g;

.p-works-archive__items {
  margin-top: g.rem(-48);

  @include g.mq("md") {
    display: flex;
    flex-wrap: wrap;
    margin-top: g.rem(-90);
    margin-left: g.rem(-48);
  }
}

.p-works-archive__item {
  margin-top: g.rem(48);

  @include g.mq("md") {
    flex: 0 1 calc(50% - #{g.rem(48)});
    margin-top: g.rem(114);
    margin-left: g.rem(48);
  }
}

.p-works-archive__pagination {
  margin-top: g.rem(80);

  @include g.mq("md") {
    margin-top: g.rem(104);
  }
}
