@use "../../../keyframes/" as a;
@use "../../../global/" as g;

.p-top-mv__content {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.p-top-mv__slide {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.p-top-mv__image {
  position: absolute;
  width: 100%;
  height: 100%;
}

.p-top-mv__image::after {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  background-color: rgba(#111, 0.2);
}

.swiper-slide-active .p-top-mv__image,
.swiper-slide-duplicate-active .p-top-mv__image,
.swiper-slide-prev .p-top-mv__image {
  animation: zoomUp 10s linear 0s 1 normal both;  
}

.p-top-mv__image img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.p-top-mv__title-wrapper {
  width: 100%;
  text-align: center;
  position: relative;
}

.p-top-mv__title {
  font-size: g.rem(28);
  font-weight: g.$bold;
  letter-spacing: calc(0.5 / 28 * 1em);
  line-height: 1;

  @include g.mq("md") {
    font-size: g.rem(50);
  }
}

.p-top-mv__subtitle {
  display: block;
  font-size: g.rem(20);
  font-weight: g.$bold;
  letter-spacing: calc(0.5 / 20 * 1em);
  line-height: 1;
  margin-top: g.rem(24);

  @include g.mq("md") {
    font-size: g.rem(25);
  }
}
