@use "../../../global/" as g;

.p-blog__subtitle {
  text-align: center;

  @include g.mq("md") {
    text-align: right;
  }
}

.p-blog__title {
  text-align: center;
  margin-top: g.rem(-16);

  @include g.mq("md") {
    margin-top: g.rem(-88);
  }
}

.p-blog__items {
  margin-top: g.rem(64);
  display: grid;
  grid-template-columns: 1fr;
  gap: g.rem(24);

  @include g.mq("md") {
    margin-top: g.rem(39);
    grid-template-columns: repeat(3, 1fr);
    gap: g.rem(25);
  }
}

.p-blog__button {
  margin-top: g.rem(24);
  text-align: center;

  @include g.mq("md") {
    margin-top: g.rem(48);
  }
}
