@use "../../../global/" as g;

.p-footer-nav__items {
  @include g.mq("md") {
    display: flex;
  }
}

.p-footer-nav__item {
  font-size: g.rem(16);
  letter-spacing: calc(1.2 / 16 * 1em);
  line-height: 1.2;
  text-align: center;

  @include g.mq("md") {
    letter-spacing: calc(2 / 16 * 1em);
  }
}

.p-footer-nav__item a {
  display: block;
  color: #fff;
  padding: g.rem(6);

  @include g.mq("md") {
    padding: g.rem(8) g.rem(16);
    position: relative;
  }
}

.p-footer-nav__item a::after {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  left: g.rem(16);
  width: calc(100% - #{g.rem(16)} * 2);
  height: g.rem(1);
  background-color: #FFEA2E;
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 0.3s;
}

.p-footer-nav__item a:focus::after,
.p-footer-nav__item a:hover::after {
  @include g.mq("md") {
    transform: scaleX(1);
  }
}
