@use "../../../global/" as g;

.p-contact-guidance {
  background-color: #3D3D3D;
}

.p-contact-guidance__subtitle {
  text-align: center;

  @include g.mq("md") {
    text-align: left;
  }
}

.p-contact-guidance__title {
  text-align: center;
  margin-top: g.rem(-16);

  @include g.mq("md") {
    margin-top: g.rem(-50);
  }
}

.p-contact-guidance__inner {
  width: 100%;
  max-width: g.rem(540 + g.strip-unit(g.$padding-sp) * 2);
  margin: g.rem(20) auto 0;
  padding: 0 g.rem(g.strip-unit(g.$padding-sp));

  @include g.mq("md") {
    max-width: g.rem(712 + g.strip-unit(g.$padding-pc) * 2);
    margin: g.rem(24) auto 0;
    padding: 0 g.rem(g.strip-unit(g.$padding-pc));
  }
}

.p-contact-guidance__text {
  font-size: g.rem(16);
  letter-spacing: calc(2 / 16 * 1em);
}

.p-contact-guidance__button {
  margin-top: g.rem(40);
  text-align: center;

  @include g.mq("md") {
    margin-top: g.rem(61);
  }
}
