@use "../../../global/" as g;

.p-contact-modal {
  background-color: #fff;
  display: none;
}

.p-contact-modal__close-icon {
  position: absolute;
  top: g.rem(12);
  right: g.rem(12);
  width: g.rem(24);
  height: g.rem(24);
  cursor: pointer;
  
  @include g.mq("md") {
    top: g.rem(24);
    right: g.rem(24);
    width: g.rem(32);
    height: g.rem(32);
  }
}

.p-contact-modal__close-icon::before {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
  width: g.rem(24);
  height: g.rem(3);
  background-color: #111;
  
  @include g.mq("md") {
    width: g.rem(32);
  }
}

.p-contact-modal__close-icon::after {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  width: g.rem(24);
  height: g.rem(3);
  background-color: #111;
  
  @include g.mq("md") {
    width: g.rem(32);
  }
}

.p-contact-modal__title {
  color: #333;
  font-size: g.rem(24);
  font-weight: g.$bold;
  letter-spacing: calc(1.2 / 24 * 1em);
  line-height: 1.2;
  padding-bottom: g.rem(16);
  text-align: center;
  position: relative;
  
  @include g.mq("md") {
    font-size: g.rem(40);
    letter-spacing: calc(2 / 40 * 1em);
    padding-bottom: g.rem(24);
  }
}

.p-contact-modal__title::after {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: g.rem(60);
  height: g.rem(3);
  background-color: #888;
  
  @include g.mq("md") {
    width: g.rem(120);
  }
}

.p-contact-modal__content {
  height: calc(100% - #{g.rem(45)} - #{g.rem(48)});
  overflow: auto;
  margin-top: g.rem(48);
  
  @include g.mq("md") {
    height: calc(100% - #{g.rem(72)} - #{g.rem(60)});
    margin-top: g.rem(60);
  }
}
