@use "../../global/" as g;

.c-button-archive-link {
  display: inline-block;
  color: #fff;
  font-size: g.rem(16);
  letter-spacing: calc(2 / 16 * 1em);
  line-height: 1.2;
  padding: g.rem(1) g.rem(17) g.rem(2) g.rem(16);
  background-color: #111;
  border: g.rem(1) solid #fff;
  transition: color 0.3s, background-color 0.3s;
}

.c-button-archive-link:focus,
.c-button-archive-link:hover {
  @include g.mq("md") {
    color: #111;
    background-color: #fff;
  }
}
