@use "../../../global/" as g;

.p-category-list {
  display: flex;
  flex-wrap: wrap;
}

.p-category-list__item {
  margin-right: g.rem(6);
  margin-bottom: g.rem(16);

  @include g.mq("md") {
    margin-right: g.rem(20);
  }
}

.p-category-list__item a {
  display: block;
  padding: g.rem(5) g.rem(8) g.rem(6);
  background-color: #111;
  color: #fff;
  font-size: g.rem(16);
  letter-spacing: calc(2 / 16 * 1em);
  line-height: 1.2;
  transition: background-color 0.3s, color 0.3s;
  
  @include g.mq("md") {
    padding: g.rem(5) g.rem(10) g.rem(6);
  }
}

.p-category-list__item a.is-active {
  background-color: #fff;
  color: #333;
}

.p-category-list__item a:focus,
.p-category-list__item a:hover,
.p-category-list__item a.is-active {
  @include g.mq("md") {
    background-color: #fff;
    color: #333;
  }
}
