@use "../../../global/" as g;

.p-gnav {
  height: inherit;
  display: none;

  @include g.mq("md") {
    display: block;
  }
}

.p-gnav__items {
  display: flex;
  height: inherit;
}

.p-gnav__item {
  font-size: g.rem(16);
  letter-spacing: calc(2 / 16 * 1em);
  height: inherit;
}

.p-gnav__item:last-child {
  margin-left: g.rem(16);
}

.p-gnav__item a {
  display: flex;
  height: inherit;
  align-items: center;
  color: #fff;
  padding: 0 g.rem(16);
  position: relative;
}

.p-gnav__item:not(:last-child) a::after {
  content: "";
  display: block;
  position: absolute;
  bottom: g.rem(8);
  left: g.rem(16);
  width: calc(100% - #{g.rem(16)} * 2);
  height: g.rem(1);
  background-color: #FFEA2E;
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 0.3s;
}

.p-gnav__item:not(:last-child) a:focus::after,
.p-gnav__item:not(:last-child) a:hover::after {
  @include g.mq("md") {
    transform: scaleX(1);
  }
}

.p-gnav__item:last-child a {
  padding: 0 g.rem(32);
  background-color: #fff;
  color: #333;
  z-index: g.$layer-default;
  overflow: hidden;
  transition: color 0.3s;
}

.p-gnav__item:last-child a:focus,
.p-gnav__item:last-child a:hover {
  @include g.mq("md") {
    color: #fff;
  }
}

.p-gnav__item:last-child a::after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  z-index: g.$layer-background;
  width: 100%;
  height: 100%;
  background-color: #333;
  transform: translateX(-100%);
  transition: transform 0.3s;
}

.p-gnav__item:last-child a:focus:after,
.p-gnav__item:last-child a:hover:after {
  @include g.mq("md") {
    transform: translateX(0);
  }
}


