@use "../../../global/" as g;

.p-lower-mv {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.p-lower-mv__image::after {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  background-color: rgba(#111, 0.2);
}

.p-lower-mv__image {
  position: absolute;
  width: 100%;
  height: 100%;
}

.p-lower-mv__image img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.p-lower-mv__title-wrapper {
  width: 100%;
  position: relative;
  text-align: center;
}

.p-lower-mv__title {
  font-size: g.rem(30);
  font-weight: g.$bold;
  letter-spacing: calc(2 / 30 * 1em);
  line-height: 1;
}
