@use "../../../global/" as g;

.p-drawer {
  background-color: #111;
  transform: translateX(105%);
  transition: transform 0.3s;
}

.p-drawer.is-open {
  transform: translateX(0);
}

.p-drawer__scroll {
  width: 100%;
  height: 100%;
  padding: g.rem(120) 0;
  overflow-y: scroll;
}

.p-drawer__nav {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
}
