@use "../../../global/" as g;

.p-header {
  background-color: rgba(#111, 0.5);
  transition: background-color 0.3s;
}

.p-header--not-transparent {
  background-color: #111;
}

.p-header.is-colored {
  background-color: rgba(#111, 1);
}

.p-header__content {
  display: flex;
  align-items: center;
}

.p-header__logo {
  width: g.rem(70);
  z-index: g.$layer-header-item;
}

.p-header__nav {
  margin-left: auto;
}

.p-header__menu-icon {
  margin-left: auto;
  z-index: g.$layer-header-item;
  height: inherit;
}

.p-header__drawer {
  position: fixed;
  top: 0;
  right: 0;
  z-index: g.$layer-drawer;
  width: 100%;
  height: 100vh;
}
