@use "../../../global/" as g;

.p-sub-philosophy-media {
  @include g.mq("md") {
    display: flex;
  }
}

.p-sub-philosophy-media:nth-child(2n) {
  flex-direction: row-reverse;
}

.p-sub-philosophy-media__image {
  width: 100vw;
  margin: 0 calc(50% - 50vw);
  position: relative;

  @include g.mq("md") {
    width: auto;
    margin: 0;
    flex: 0 0 calc(550 / #{g.strip-unit(g.$l-inner)} * 100%);
  }
}

.p-sub-philosophy-media__image::before {
  content: "";
  display: block;
  padding-top: g.aspect-ratio(261, 375);

  @include g.mq("md") {
    padding-top: g.aspect-ratio(367, 550);
  }
}

.p-sub-philosophy-media__image img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.p-sub-philosophy-media__body {
  padding: g.rem(24) 0 0;

  @include g.mq("md") {
    padding: g.rem(20) 0 0 g.rem(30);
    flex: 0 1 auto;
  }
}

.p-sub-philosophy-media:nth-child(2n) .p-sub-philosophy-media__body {
  @include g.mq("md") {
    padding: g.rem(20) g.rem(30) 0 0;
  }
}

.p-sub-philosophy-media__title {
  font-size: g.rem(20);
  letter-spacing: calc(2 / 20 * 1em);
  text-align: center;

  @include g.mq("md") {
    font-size: g.rem(25);
    letter-spacing: calc(0.5 / 25 * 1em);
    text-align: left;
  }
}

.p-sub-philosophy-media__text-wrapper {
  margin-top: g.rem(24);

  @include g.mq("md") {
    margin-top: g.rem(40);
  }
}

.p-sub-philosophy-media__text {
  font-size: g.rem(16);
  letter-spacing: calc(2 / 16 * 1em);

  @include g.mq("md") {
    letter-spacing: calc(0.5 / 16 * 1em);
    line-height: g.lh(35, 16);
  }
}
