@use "../../../global/" as g;

.p-works-detail__inner {
  width: 100%;
  max-width: g.rem(540 + g.strip-unit(g.$padding-sp) * 2);
  margin: 0 auto;
  padding: 0 g.rem(g.strip-unit(g.$padding-sp));

  @include g.mq("md") {
    max-width: g.rem(800 + g.strip-unit(g.$padding-pc) * 2);
    padding: 0 g.rem(g.strip-unit(g.$padding-pc));
  }
}

.p-works-detail__title {
  font-size: g.rem(20);
  letter-spacing: calc(0.4 / 20 * 1em);
  line-height: g.lh(35, 20);

  @include g.mq("md") {
    font-size: g.rem(23);
    line-height: g.lh(35, 23);
  }
}

.p-works-detail__meta {
  display: flex;
  align-items: center;
  margin-top: g.rem(16);

  @include g.mq("md") {
    margin-top: g.rem(32);
  }
}

.p-works-detail__published {
  font-size: g.rem(16);
  letter-spacing: calc(0.4 / 16 * 1em);
  line-height: 2;
}

.p-works-detail__category {
  margin-left: g.rem(30);
}

.p-works-detail__gallery {
  width: 100vw;
  margin: g.rem(16) calc(50% - 50vw) 0;

  @include g.mq("md") {
    width: auto;
    margin: g.rem(32) 0 0;
  }
}

.p-works-detail__point-list {
  margin-top: g.rem(32);

  @include g.mq("md") {
    margin-top: g.rem(80);
  }
}

.p-works-detail__nav {
  margin-top: g.rem(80);

  @include g.mq("md") {
    margin-top: g.rem(104);
  }
}
