@use "../../../global/" as g;

.p-overview-map__inner {
  width: 100%;
  max-width: 100%;
  margin: 0 auto;

  @include g.mq("md") {
    max-width: g.rem(g.strip-unit(g.$l-inner) + g.strip-unit(g.$padding-pc) * 2);
    padding: 0 g.rem(g.strip-unit(g.$padding-pc));
  }
}

.p-overview-map__content {
  position: relative;
  padding-top: g.aspect-ratio(300, 375);
  overflow: hidden;
  
  @include g.mq("md") {
    padding-top: g.aspect-ratio(367, 1100);
  }
}

.p-overview-map__content iframe {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 250%;
  object-fit: cover;
  object-position: center;
}
