@use "../../../global/" as g;

.p-top-news__items {
  @include g.mq("md") {
    flex: 0 1 g.rem(860);
  }
}

.p-top-news__content {
  @include g.mq("md") {
    display: flex;
    justify-content: center;
  }
}

.p-top-news__button {
  margin-top: g.rem(28);
  text-align: center;
  
  @include g.mq("md") {
    margin-top: 0;
    text-align: right;
    flex: 0 0 g.rem(182);
  }
}
