@use "../../../global/" as g;

.p-top-info {
  display: block;

  @include g.mq("md") {
    display: flex;
  }
}

.p-top-info__meta {
  width: 100%;
  max-width: g.rem(211);
  display: flex;
  
  @include g.mq("md") {
    width: auto;
    max-width: initial;
    flex: 0 0 g.rem(227);
  }
}

.p-top-info__published {
  display: inline-block;
  color: #fff;
  font-size: g.rem(16);
  line-height: 1.2;
  padding-top: g.rem(6);
}

.p-top-info__category-wrapper {
  margin-left: auto;
}

.p-top-info__category {
  display: inline-block;
  color: #fff;
  font-size: g.rem(14);
  line-height: 1.2;
  border: g.rem(1) solid #fff;
  padding: g.rem(6) g.rem(21);
}

.p-top-info__title {
  font-size: g.rem(16);
  margin-top: g.rem(16);
  
  @include g.mq("md") {
    margin-top: 0;
    margin-left: g.rem(61);
    padding-top: g.rem(3);
    flex: 0 1 calc(100% - #{g.rem(227)});
  }
}

.p-top-info__title {
  color: #fff;
  transition: text-decoration 0.3s;
}

.p-top-info:focus .p-top-info__title,
.p-top-info:hover .p-top-info__title {
  @include g.mq("md") {
    text-decoration: underline;
  }
}
