@use "../../../global/" as g;

.p-related-post__title {
  font-size: g.rem(16);
  letter-spacing: calc(2 / 16 * 1em);
  line-height: 1.2;
  text-align: center;
  padding: g.rem(10) g.rem(20) g.rem(11);
  background-color: #333;
  border: g.rem(1) solid #fff;
}

.p-related-post__items {
  display: grid;
  grid-template-columns: 1fr;
  gap: g.rem(24);
  margin-top: g.rem(32);

  @include g.mq("md") {
    grid-template-columns: repeat(2, 1fr);
    gap: g.rem(32);
    margin-top: g.rem(80);
  }

  @include g.mq("lg") {
    grid-template-columns: repeat(4, 1fr);
  }
}
