@use "../../global/" as g;

.l-diagonal-line__downward {
  position: relative;
  overflow: hidden;
  margin-top: g.rem(107);

  @include g.mq("md") {
    margin-top: g.rem(87);
    padding-top: g.rem(107);
  }
}

.l-diagonal-line__downward::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  z-index: g.$layer-background;
  width: g.rem(1);
  height: 200%;
  background-color: #F9F871;
  transform: rotate(-29deg);
  transform-origin: left top;

  @include g.mq("md") {
    transform: rotate(-45deg);
  }
}

.l-diagonal-line__upward {
  position: relative;
  overflow: hidden;
  padding-top: g.rem(78);
  padding-bottom: g.rem(120);

  @include g.mq("md") {
    padding-top: g.rem(164);
    padding-bottom: g.rem(150);
  }
}

.l-diagonal-line__upward::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  right: 37%;
  z-index: g.$layer-background;
  width: g.rem(1);
  height: 200%;
  background-color: #F9F871;
  transform: rotate(15deg);
  transform-origin: right top;

  @include g.mq("md") {
    right: 4%;
    transform: rotate(45deg);
  }
}
