@use "../../global/" as g;

.c-button-more {
  display: inline-block;
  width: 100%;
  max-width: g.rem(360);
  border: g.rem(1) solid #fff;
  background-color: #333;
  padding: g.rem(12) g.rem(24) g.rem(12) g.rem(24);
  color: #fff;
  font-size: g.rem(20);
  letter-spacing: calc(2 / 20 * 1em);
  line-height: 1.2;
  text-align: center;
  
  @include g.mq("md") {
    padding: g.rem(19) g.rem(40) g.rem(20);
    font-size: g.rem(16);
    font-weight: g.$medium;
    letter-spacing: calc(2 / 16 * 1em);
    overflow: hidden;
    position: relative;
    z-index: g.$layer-default;
  }
}

.c-button-more:focus,
.c-button-more:hover {
  @include g.mq("md") {
    color: #333;
  }
}

.c-button-more::after {
  @include g.mq("md") {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    z-index: g.$layer-background;
    width: calc(100% - #{g.rem(2)});
    width: 100%;
    height: calc(100% - #{g.rem(2)});
    height: 100%;
    background-color: #fff;
    transform: translateX(-100%);
    transition: transform 0.3s;
  }
}

.c-button-more:focus::after,
.c-button-more:hover::after {
  @include g.mq("md") {
    transform: translateX(0);
  }
}
