@use "../../../global/" as g;

.p-works-media {
  background: linear-gradient(to bottom, transparent 0%, transparent 34%, #333 34%, #333 100%);
  
  @include g.mq("md") {
    background: linear-gradient(to right, transparent 0%, transparent 30%, #333 30%, #333 100%);
  }
}

.p-works-media__content {
  @include g.mq("md") {
    display: flex;
    align-items: center;
  }
}

.p-works-media__head {
  width: 100vw;
  margin: 0 calc(50% - 50vw);
  overflow: hidden;

  @include g.mq("md") {
    width: auto;
    margin: 0;
    flex: 0 0 calc(557 / #{g.strip-unit(g.$l-inner)} * 100%);
  }
}

.p-works-media__image {
  position: relative;
}

.p-works-media__image::before {
  content: "";
  display: block;
  padding-top: g.aspect-ratio(261, 375);

  @include g.mq("md") {
    padding-top: g.aspect-ratio(389, 557);
  }
}

.p-works-media__image::after {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  background-color: rgba(#111, 0.2);
}

.p-works-media__image img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.p-works-media__body {
  @include g.mq("md") {
    flex: 0 1 auto;
    margin-left: calc(32 / #{g.strip-unit(g.$l-inner)} * 100%);
  }
}

.p-works-media__title {
  font-size: g.rem(20);
  letter-spacing: calc(2 / 20 * 1em);
  line-height: 1.2;
  text-align: center;

  @include g.mq("md") {
    font-size: g.rem(25);
    text-align: left;
  }
}

.p-works-media__text {
  font-size: g.rem(16);
  letter-spacing: calc(2 / 16 * 1em);
  margin-top: g.rem(24);

  @include g.mq("md") {
    margin-top: g.rem(48);
    line-height: g.lh(35, 16);
  }
}

.p-works-media__button {
  margin-top: g.rem(16);
  text-align: center;

  @include g.mq("md") {
    margin-top: g.rem(48);
  }
}
