@use "../../global/" as g;

.c-button-submit {
  -webkit-appearance: none;
	background-color: #fff;
	border: none;
	border-radius: 0;
	color: #333;
	cursor: pointer;
	display: inline-block;
  font-family: inherit;
	font-size: g.rem(20);
  letter-spacing: calc(2 / 20 * 1em);
  line-height: 1.2;
  max-width: g.rem(360);
	padding: g.rem(13) g.rem(20);
	text-decoration: none;
  transition: background-color 0.3s, color 0.3s;
  width: 100%;

  @include g.mq("md") {
    font-size: g.rem(16);
    padding: g.rem(20) g.rem(40) g.rem(21);
  }
}

.c-button-submit:focus,
.c-button-submit:hover {
  @include g.mq("md") {
    background-color: #333;
    color: #fff;
  }
}
