@use "../../../global/" as g;

.p-footer-upper__inner {
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
  padding: 0 g.rem(g.strip-unit(g.$padding-sp));

  @include g.mq("md") {
    padding: 0 g.rem(16) 0 g.rem(g.strip-unit(g.$padding-pc));
  }
}

.p-footer-upper__content {
  @include g.mq("md") {
    display: flex;
    align-items: center;
  }
}

.p-footer-upper__logo {
  width: g.rem(70);
  margin: 0 auto;

  @include g.mq("md") {
    margin: 0;
  }
}

.p-footer-upper__nav {
  margin-top: g.rem(5);

  @include g.mq("md") {
    margin-top: 0;
    margin-left: auto;
  }
}
