@use "../../global/" as g;

.c-new-badge {
  display: block;
  width: g.rem(60);
  height: g.rem(60);
  border-radius: 50%;
  background-color: #FFBA6A;
  color: #333;
  font-size: g.rem(16);
  letter-spacing: calc(2 / 16 * 1em);
  line-height: g.rem(60);
  text-align: center;
  text-transform: capitalize;
}
