@use "../../../global/" as g;

.p-main-philosophy__inner {
  width: 100%;
  max-width: g.rem(568 + g.strip-unit(g.$padding-sp) * 2);
  margin: 0 auto;
  padding: 0 g.rem(g.strip-unit(g.$padding-sp));

  @include g.mq("md") {
    max-width: g.rem(568 + g.strip-unit(g.$padding-pc) * 2);
    padding: 0 g.rem(g.strip-unit(g.$padding-pc));
  }
}

.p-main-philosophy__title {
  font-size: g.rem(20);
  letter-spacing: calc(2 / 20 * 1em);
  text-align: center;

  @include g.mq("md") {
    font-size: g.rem(25);
  }
}

.p-main-philosophy__text-wrapper {
  margin-top: g.rem(24);
}

.p-main-philosophy__text {
  font-size: g.rem(16);
  letter-spacing: calc(2 / 16 * 1em);

  @include g.mq("md") {
    line-height: g.lh(35, 16);
  }
}
