@use "../../global/" as g;

.c-category-label {
  display: inline-block;
  border: g.rem(1) solid #fff;
  background-color: #111;
  padding: g.rem(9) g.rem(18) g.rem(10);
  color: #fff;
  font-size: g.rem(16);
  letter-spacing: calc(2 / 16 * 1em);
  line-height: 1.2;
  transition: background-color 0.3s, color 0.3s;
}

.c-category-label:focus,
.c-category-label:hover {
  @include g.mq("md") {
    background-color: #fff;
    color: #111;
  }
}
