@use "../../../global/" as g;

.p-contact-thanks__inner {
  width: 100%;
  max-width: g.rem(540 + g.strip-unit(g.$padding-sp) * 2);
  margin: 0 auto;
  padding: 0 g.rem(g.strip-unit(g.$padding-sp));

  @include g.mq("md") {
    max-width: g.rem(800 + g.strip-unit(g.$padding-pc) * 2);
    padding: 0 g.rem(g.strip-unit(g.$padding-pc));
  }
}

.p-contact-thanks__title {
  font-size: g.rem(20);
  letter-spacing: calc(2 / 20 * 1em);
  line-height: 1.2;
  text-align: center;
  
  @include g.mq("md") {
    font-size: g.rem(30);
    letter-spacing: calc(2 / 30 * 1em);
  }
}

.p-contact-thanks__text {
  font-size: g.rem(20);
  letter-spacing: calc(2 / 20 * 1em);
  text-align: center;
  margin-top: g.rem(80);
  
  @include g.mq("md") {
    font-size: g.rem(30);
    letter-spacing: calc(2 / 30 * 1em);
  }
}

.p-contact-thanks__button {
  text-align: center;
  margin-top: g.rem(80);
}
