@use "../../../global/" as g;

.p-blog-detail__inner {
  width: 100%;
  max-width: g.rem(540 + g.strip-unit(g.$padding-sp) * 2);
  margin: 0 auto;
  padding: 0 g.rem(g.strip-unit(g.$padding-sp));

  @include g.mq("md") {
    max-width: g.rem(800 + g.strip-unit(g.$padding-pc) * 2);
    padding: 0 g.rem(g.strip-unit(g.$padding-pc));
  }
}

.p-blog-detail__title {
  font-size: g.rem(20);
  letter-spacing: calc(0.4 / 20 * 1em);
  line-height: g.lh(35, 20);
  
  @include g.mq("md") {
    font-size: g.rem(23);
    letter-spacing: calc(0.4 / 23 * 1em);
    line-height: g.lh(35, 23);
  }
}

.p-blog-detail__meta {
  display: flex;
  align-items: center;
  margin-top: g.rem(16);

  @include g.mq("md") {
    margin-top: g.rem(32);
  }
}

.p-blog-detail__published {
  display: block;
  font-size: g.rem(16);
  letter-spacing: calc(0.4 / 16 * 1em);
  line-height: 1.2;

  @include g.mq("md") {
    line-height: 2;
  }
}

.p-blog-detail__category {
  margin-left: g.rem(24);
}

.p-blog-detail .p-blog-detail__eye-catch {
  width: 100vw;
  margin: g.rem(16) calc(50% - 50vw) 0;
  position: relative;

  @include g.mq("md") {
    width: auto;
    margin: g.rem(32) 0 0;
  }
}

.p-blog-detail .p-blog-detail__eye-catch::before {
  content: "";
  display: block;
  padding-top: g.aspect-ratio(251, 375);

  @include g.mq("md") {
    padding-top: g.aspect-ratio(427, 800);
  }
}

.p-blog-detail .p-blog-detail__eye-catch img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.p-blog-detail h2 {
  font-size: g.rem(20);
  letter-spacing: calc(2 / 20 * 1em);
  line-height: 1.2;
  padding-left: g.rem(9);
  position: relative;
  margin-top: g.rem(24);

  @include g.mq("md") {
    margin-top: g.rem(32);
  }
}

.p-blog-detail h2::before {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: g.rem(3);
  height: g.rem(29);
  background-color: #C4C4C4;
}

.p-blog-detail h3 {
  display: inline-block;
  font-size: g.rem(18);
  letter-spacing: calc(2 / 18 * 1em);
  line-height: 1.2;
  padding-bottom: g.rem(6);
  padding-left: g.rem(9);
  position: relative;
  margin-top: g.rem(24);

  @include g.mq("md") {
    margin-top: g.rem(32);
  }
}

.p-blog-detail h3::before {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  left: g.rem(9);
  width: calc(100% - #{g.rem(9)});
  height: g.rem(3);
  background-color: #fff;
}

.p-blog-detail h3::after {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  left: g.rem(9);
  width: g.rem(73);
  height: g.rem(3);
  background-color: #FFEA2E;
}

.p-blog-detail p {
  font-size: g.rem(16);
  letter-spacing: calc(0.4 / 16 * 1em);
  line-height: g.lh(35, 16);
  margin-top: g.rem(24);

  @include g.mq("md") {
    margin-top: g.rem(32);
  }
}

.p-blog-detail figure {
  width: 100vw;
  margin: g.rem(24) calc(50% - 50vw) 0;
  position: relative;

  @include g.mq("md") {
    width: auto;
    margin: g.rem(32) 0 0;
  }
}

.p-blog-detail figure::before {
  content: "";
  display: block;
  padding-top: g.aspect-ratio(251, 375);

  @include g.mq("md") {
    padding-top: g.aspect-ratio(427, 800);
  }
}

.p-blog-detail figure img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.p-blog-detail ul,
.p-blog-detail ol {
  padding-left: g.rem(20);
  margin-top: g.rem(24);

  @include g.mq("md") {
    margin-top: g.rem(32);
  }
}

.p-blog-detail ul {
  list-style-type: disc;
}

.p-blog-detail ol {
  list-style-type: decimal;
}

.p-blog-detail li {
  font-size: g.rem(16);
  letter-spacing: calc(0.4 / 16 * 1em);
  line-height: g.lh(35, 16);
}

.p-blog-detail__nav {
  margin-top: g.rem(80);

  @include g.mq("md") {
    margin-top: g.rem(104);
  }
}
