@use "../../../global/" as g;

.p-content__subtitle {
  text-align: center;

  @include g.mq("md") {
    text-align: left;
  }
}

.p-content__title {
  text-align: center;
  margin-top: g.rem(-16);

  @include g.mq("md") {
    margin-top: g.rem(-102);
  }
}

.p-content__items {
  display: flex;
  flex-wrap: wrap;
  width: 100vw;
  margin: g.rem(48) calc(50% - 50vw) 0;
}

.p-content__item {
  flex: 0 1 50%;

  @include g.mq("md") {
    flex: 0 1 25%;
  }
}
