@use "../../../global/" as g;

.p-works-card {
  display: block;
}

.p-works-card__image {
  width: 100vw;
  margin: 0 calc(50% - 50vw);
  position: relative;
  overflow: hidden;

  @include g.mq("md") {
    width: auto;
    margin: 0;
  }
}

.p-works-card__image::before {
  content: "";
  display: block;
  padding-top: g.aspect-ratio(249, 375);

  @include g.mq("md") {
    padding-top: g.aspect-ratio(349, 525);
  }
}

.p-works-card__image img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  transition: transform 0.3s;
}

.p-works-card:focus .p-works-card__image img,
.p-works-card:hover .p-works-card__image img {
  @include g.mq("md") {
    transform: translate(-50%, -50%) scale(1.1);
  }
}

.p-works-card__category {
  display: inline-block;
  position: absolute;
  top: 0;
  right: 0;
  color: #fff;
  font-size: g.rem(16);
  letter-spacing: calc(2 / 16 * 1em);
  line-height: 1.2;
  background-color: #111;
  padding: g.rem(10) g.rem(19) g.rem(11);
}

.p-works-card__body {
  margin-top: g.rem(24);
}

.p-works-card__title {
  color: #fff;
  font-size: g.rem(20);
  font-weight: g.$medium;
  letter-spacing: calc(2 / 20 * 1em);
  line-height: g.lh(35, 20);
  text-align: center;
  transition: color 0.3s;
}

.p-works-card:focus .p-works-card__title,
.p-works-card:hover .p-works-card__title {
  @include g.mq("md") {
    color: #FFEA2E;
  }
}
