@use "variables" as v;
@use "sass:map";

// pxをremに変換
@function rem($pixels) {
  @return $pixels / 16 * 1rem;
}

// pxをvwに変換
@function vw($window_width, $size) {
	@return $size / $window_width * 100vw;
}

// ---
// メディアクエリー
// ---
@mixin mq($breakpoint: "md") {
	@media #{map.get(v.$breakpoints, $breakpoint)} {
		@content;
	}
}

// 数値のみを取り出す
@function strip-unit($number) {
  @if type-of($number) == "number" and not unitless($number) {
    @return $number / ($number * 0 + 1);
  }
  @return $number;
}

// line-heightの算出
@function lh($height: 24, $font-size: 16) {
  @return $height / $font-size;
}

// letter-spacingの算出
@function ls($av) {
  @return $av / 1000 * 1em;
}

// 画像のアスペクト比
@function aspect-ratio($height, $side) {
  @return $height / $side * 100%;
}
