@use "../../../global/" as g;

.p-philosophy-link {
  display: block;
  position: relative;
}

.p-philosophy-link::after {
  @include g.mq("md") {
    content: "";
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    background-color: #fff;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s, visibility 0.3s;
  }
}

.p-philosophy-link:focus::after,
.p-philosophy-link:hover::after {
  @include g.mq("md") {
    opacity: 0.6;
    visibility: visible;
  }
}

.p-philosophy-link__image {
  position: relative;
}

.p-philosophy-link__image::before {
  content: "";
  display: block;
  padding-top: g.aspect-ratio(168, 188);

  @include g.mq("md") {
    padding-top: 100%;
  }
}

.p-philosophy-link__image img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
}

.p-philosophy-link__title-wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: g.$layer-default;
  transform: translate(-50%, -50%);
  width: 100%;
  text-align: center;
}

.p-philosophy-link__title {
  color: #fff;
  font-size: g.rem(20);
  letter-spacing: calc(2 / 20 * 1em);
  line-height: 1.2;

  @include g.mq("md") {
    font-size: g.rem(25);
  }
}
